*,
*:before,
*:after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  font-size: 16px;
  font-family: sans-serif;
  line-height: 1.5;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  margin-bottom: 1.875rem;
}

h1 {
  font-size: 2.25em;
}

h2 {
  font-size: 1.875em;
}

h3 {
  font-size: 1.5em;
}

h4 {
  font-size: 1.125em;
}

h5 {
  font-size: 0.875em;
}

h6 {
  font-size: 0.75em;
}

input,
select,
textarea {
  width: 100%;
  border: 1px solid #e5e5e5;
  border-radius: 0;
}

input[type="submit"] {
  width: auto;
}

.o-debug {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.o-debug .o-container {
  position: relative;
  min-height: 100%;
}

.o-debug .o-row {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
}

.o-debug .o-col {
  position: relative;
  height: 100%;
}

.o-debug .o-col:before {
  content: '';
  position: absolute;
  top: 0;
  left: 15px;
  right: 15px;
  width: auto;
  height: inherit;
  background-color: rgba(217, 237, 247, 0.3);
}

.o-container {
  max-width: 75rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0.9375rem;
  padding-right: 0.9375rem;
}

.o-row {
  margin-left: -0.9375rem;
  margin-right: -0.9375rem;
}

.o-row:before,
.o-row:after {
  content: ' ';
  display: table;
}

.o-row:after {
  clear: both;
}

.o-col {
  float: left;
